import _ from 'lodash';
import api from 'utils/api'
function sortBy(arr) { //排序初始化
    return _.sortBy(arr, (item) => {
        return item.sortId
    })
}

function getRoles(callback, type, modules) { //得到角色列表
    api.get(`/role`).then(res => {
        let datas = res.data.map(item => {
            const obj = {
                id: item.id,
                dKey: item.id,
                dValue: item.sign
            }
            return obj
        })
        if( type != 'filter' ){
            datas.unshift({
                id: '',
                dKey: '',
                dValue: '全部'
            })
        }
        if ( modules === 'less' ){
            const arr = ['超级管理员','学员'];
            datas = datas.filter(item => {
                return arr.indexOf(item.dValue) == -1
            })
        }
        if (callback) callback(datas)
        return res.data;
    })
    .catch(err => {
        console.log(err)
    });
    return [];
}

