function filterTeachers(id, callback, type) { //得到筛选带班老师用户列表
    api.get(`/user?schoolId=${id}&roleId=3&page=0&size=1000000`).then(res => {
        let datas = res.data.list.map(item => {
            const obj = {
                id: item.id,
                dKey: item.account,
                dValue: item.name
            }
            return obj
        })
        if( type != 'filter' ){
            datas.unshift({
                id: '',
                dKey: '',
                dValue: '全部'
            })
        }

        let obj = [];
        datas = datas.filter(item => {
            if(obj.indexOf(item.dKey) == -1){
                obj.push(item.dKey);
                return true
            }
            return false;
        })
        
        if (callback) callback(datas)
        return res.data;
    })
    .catch(err => {
        console.log(err)
    });
}

