<template>
    <el-container v-loading="loading">
        <div class="rise x add">
            <div class="title">{{title}}</div>
            <div class="content">
                <div class="item" v-for="(item, index) in data" :key="index">
                    <span>{{item.name}}</span>
                    <!-- input -->
                    <div v-if="!item.type">
                        <el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'account'" v-model="account" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'password'" v-model="password" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'email'" v-model="email" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'score'" v-model="score" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'memo'" v-model="memo" :placeholder="item.placeholder"/>
                    </div>
                    <!-- select -->
                    <div v-if="item.type === 'select'">
                        <el-select v-if="item.module === 'schoolId'" v-model="schoolId" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                        <el-select v-if="item.module === 'classId'" v-model="classId" placeholder="全部">
                            <el-option
                                v-for="item in classOptions"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                    </div>
                </div>
            </div>
            <div class="btn-list">
                <el-button @click="cancle()" class="btn">取消</el-button>
                <el-button @click="save()" class="btn">保存</el-button>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { getSchool, getClass, filterClass } from "data/listBody";
import md5 from "md5";
const titles = ["新建", "编辑"];
const tips = ['请补全信息后提交'];
const defaultV = [
  {
    name: "校区：",
    placeholder: "请选择校区",
    module: "schoolId",
    type: "select"
  },
  {
    name: "班级：",
    placeholder: "请选择班级",
    module: "classId",
    type: "select"
  },
  { name: "学员姓名：", placeholder: "请输入学员姓名", module: "name" },
  { name: "学员账号：", placeholder: "请输入学员账号", module: "account" },
  { name: "学员邮箱：", placeholder: "请输入学员邮箱", module: "email" },
//   { name: "学员积分：", placeholder: "请输入学员积分", module: "score" },
  { name: "备注：", placeholder: "请输入备注", module: "memo" },
];

export default {
  name: "add",
  data() {
    return {
      title: "",
      id: "",
      data: defaultV,
      loading: false,
      name: "",
      account: "",
      schoolId: "",
      email: "",
      score: "",
      classId: "",
      classOptions : "",
      change: true,
      memo:'',
    };
  },
  mounted() {
    this.dataInit();
    this.dropInit();
  },
  methods: {
    dropInit() {
      getSchool(res => {
        let leftMenu = this.data.map(item => {
          if (item.module === "schoolId") item.options = res;
          return item;
        });
        this.data = leftMenu;
      }, "filter");
    },
    dataInit() {
      const { type, id } = this.$route.query;
      this.type = type;
      this.id = id;
      if (type !== "edit") return (this.title = titles[0]);
      this.title = titles[1];
      this.detailAjax();
    },
    editInit(res) {
      const { name, account, password, schoolId, classId, mobile, email, memo, score, roleId } = res.data;
      this.name = name;
      this.account = account;
      this.password = password;
      this.schoolId = schoolId;
      this.classId = classId;
      this.mobile = mobile;
      this.email = email;
      this.memo = memo;
    //   this.score = score;
      this.roleId = roleId;
    },
    detailAjax() {
      //查看详情
      this.loading = true;
      this.change = false;
      setTimeout(() => {
        this.change = true;
      },1000)
      this.api.get(`/user/${this.id}`).then(res => {
          this.loading = false;
          this.editInit(res);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.message);
        });
    },
    editAjax() {
      if( !this.account || !this.schoolId || !this.classId ) return this.$message(tips[0]);
      const params = {
        name: this.name,
        account: this.account,
        password: md5(this.account).toLowerCase(),
        schoolId: this.schoolId,
        classId: this.classId,
        mobile: this.account,
        email: this.email,
        memo: this.memo,
        score: this.score,
        roleId: '2',
      };
      const axiosType = ["post", "put"];
      let type = axiosType[0];
      if (this.type === "edit") {
        params.id = this.id;
        type = axiosType[1];
      }
      this.loading = true;
      this.api[type](`/user`, params, "form").then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message(err.message);
        });
    },
    save() {
      this.editAjax();
    },
    cancle() {
      //取消
      this.$router.go(-1);
    }
  },
  watch: {
    schoolId(newV, oldV){
        if(this.change) this.classId = '';
        filterClass(newV, (res) => {
            this.classOptions = res;
        }, 'filter')
    }
  }
};
</script>

<style>
.el-message{
  top: 80px;
}
</style>
